import React, {useCallback} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";
import {useForm} from "react-hook-form";
import {StakeInputWidget} from "../../../widget/base/stake_input";
import {StakeButtonWidget} from "./button";
import {useStakeContext} from "../../../provider/stake_provider";
import {useUserInfo, useWithdraw} from "../../../server/chef";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useSendModal} from "../../../widget/modal/send_modal";
import {ButtonWidget} from "../../../widget/base/button";

export const BottomLeft: React.FC = () => {
    const {register, errors, setValue, handleSubmit} = useForm();

    const {pid,amount,rewardDebt} = useStakeContext();
    const maxNumber = getBalanceNumber(amount);

    // 提交请求
    const [facethDeposit] = useWithdraw();

    const sendFn = useSendModal(facethDeposit);
    const handleLogin = useCallback(async (data: string) => {
        let _number = getMaxNumber(data, amount);
        await sendFn(pid, _number)
        setValue('amount', 0);
    }, [pid,amount]);
    return (
        <GridStyle item md={6}>
            <TopStyle>
                <p>Withdraw</p>
                <span>{getBalanceFormat(amount)} Available</span>
            </TopStyle>


            <div className={'padding'}>
                <StakeInputWidget label={'Amount to Unstake'}
                                  name={'amount'}
                                  setValue={(ev) => {
                                      setValue(ev, maxNumber);
                                  }}
                                  errors={errors}
                                  inputRef={register({
                                      required: 'no Enter the number is dame',
                                  })}
                                  placeholder={'0.00'}
                />
            </div>
            <ButtonWidget onClick={handleSubmit(({amount}) => handleLogin(amount))}  text={"Confirm Withdraw & Claim Rewards"}/>


            <div className={'bottom'}>
                <p>Rewards</p>
                <ButtonWidget onClick={()=>handleLogin('0')}  text={"Claim Rewards"}/>

            </div>

        </GridStyle>
    );
}

const GridStyle = styled(Grid)`
  padding-left: 25px;
  box-sizing: border-box;

  .padding {
    padding: 20px 0;
  }

  .bottom {
    border-top: 1px solid #D7DBE1;
    margin-top: 20px;
    padding-top: 20px;

    p {
      font-size: 14px;
      font-weight: 400;
      color: #7F7F99;
      margin-bottom: 12px;
    }

  }
`

const TopStyle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #D7DBE1;


  p {
    font-size: 14px;
    font-weight: 400;
    color: #7F7F99;
  }

  span {
    font-size: 14px;
    font-weight: 400;
    color: #090B4B;
  }
`
