import React, {createContext, useContext} from "react";
import {Contract} from "web3-eth-contract";
import BigNumber from "bignumber.js";


export const StakeContext = createContext<StakeInterface>({
    address: '',
    contract: undefined,
    pid:'',
    amount:new  BigNumber(0),
    rewardDebt:new  BigNumber(0),
});

export interface StakeInterface {
    address: string,
    contract: Contract | any,
    pid:string,
    amount:BigNumber,
    rewardDebt:BigNumber,
}


export const useStakeContext = () => {
    let data = useContext(StakeContext);
    return data;
}
