import React from "react";
import styled from "styled-components";
import {useAllowance, useApprove} from "../../../utils/token_server";
import {useModel} from "../../../hook/use_model";
import {SendModal} from "../../../widget/modal/send_modal";
import {Contract} from "web3-eth-contract";
import {ButtonWidget} from "../../../widget/base/button";
import {useStakeContext} from "../../../provider/stake_provider";
import {useWeb3} from "../../../provider/web3_provider";

export const StakeButtonWidget: React.FC<{ text: string, onClick?: Function }> = ({
                                                                                                                                                    text,
                                                                                                                                                    onClick,


                                                                                                                                                }) => {
    const {address, contract} = useStakeContext();
    const {contrcts} = useWeb3();

    const {chefContract, chefAddress} = contrcts;

    const allowance = useAllowance(contract, chefAddress); // 检查权限
    const [approve] = useApprove(contract, address, chefAddress); // 授权方法

    const [handleApprove] = useModel(<SendModal confirmationFn={approve}/>); // 申请授权
    return (
        <div>
            {allowance <= 0 ?
                <ButtonStyle onClick={handleApprove}>Approve</ButtonStyle> :  <ButtonStyle onClick={
                () => onClick && onClick()
            }>
                {text}
            </ButtonStyle>}
        </div>

    );
}


const ButtonStyle = styled.div`
  background-color: #272CBC;
  border-radius: 5px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  color: #FFFFFF;

`
