import React, {createRef, useRef, useState} from "react";
import {TextField, InputBase, withStyles, MenuItem, Select, Input} from "@material-ui/core";
import styled from "styled-components";


interface InputInterFace {
    label: string,
    unit?: string,
    onChange?: (e: any) => void,
    placeholder: string,
    setValue?: (ev: string) => void,
    inputRef: React.Ref<any>,
    errors: any;
    name?: string,
    buttonText?: string,
    type?: string,

    // max:string,
}


export const InputWidget: React.FC<InputInterFace> = ({
                                                          label,
                                                          placeholder,
                                                          setValue,
                                                          unit,
                                                          inputRef,
                                                          errors,
                                                          name,
                                                          buttonText = 'Max',
                                                          type,
                                                          onChange,
                                                          children
                                                      }) => {


    return (
        <>
            <LabelStyle>{label}</LabelStyle>
            <FlexStyle>
                <InputStyle>
                    <TextField name={name || label}
                               error={Boolean(errors[name || label])}
                               placeholder={placeholder}
                               unselectable={'off'}
                               fullWidth={true}
                               inputRef={inputRef}
                               type={type}
                               onChange={(ev) => onChange && onChange(ev)}
                    />
                    {
                        unit ?
                            <span>{unit}</span> : children
                    }
                </InputStyle>

                {setValue ?
                    <>
                        <ButtonStyle onClick={() => setValue(name || label)}>
                            Max
                        </ButtonStyle>
                    </>
                    : <div/>}
            </FlexStyle>
            <HelperTextStyle>{errors[name || label] ? errors[name || label].message : ''}</HelperTextStyle>
        </>
    );
}

const ButtonStyle = styled.div`
  background-color: #CFD1F8;
  padding: 4px 15px;
  box-sizing: border-box;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 400;
  color: #272CBC;
  margin-right: 15px;

`
const HelperTextStyle = styled.div`
  color: red;
  font-size: 14px;
  margin-top: 5px;
  margin-bottom: 5px;
`;

const LabelStyle = styled.h2`
  font-size: 14px;
  font-weight: 400;
  color: #21247F;
  line-height: 1;
`


const FlexStyle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #D5D6E9;
  border-radius: 8px;
  margin: 10px 0;
  box-sizing: border-box;

  .MuiInput-underline:before, .MuiInput-underline:after {
    display: none;
  }

`

const InputStyle = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
  border: 1px solid transparent;
  font-size: 14px;
  font-weight: 600;
  clip-path: inset(0 round 4px);
  padding: 5px 10px;

  span {
    font-size: 14px;
    font-weight: 400;
    color: #7688B7;
    display: inline-block;
    padding-left: 20px;
  }

  .MuiInputBase-input {
    

    &::-webkit-input-placeholder {
      text-align: start;
    }


  }
`
