import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useState} from "react";
import {useWallet} from "use-wallet";
import BigNumber from "bignumber.js";

export const useDeposit = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.chefAddress);
    const facethBack = useCallback(async (pid: number, amount: string) => {
        let method = contrcts.chefContract.methods.deposit(pid, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}

export const useWithdraw = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.chefAddress);
    const facethBack = useCallback(async (pid: number, amount: string) => {
        let method = contrcts.chefContract.methods.withdraw(pid, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}


export const useUserInfo = (pid:string) => {
    const {contrcts, blockNumber} = useWeb3();
    const {account} = useWallet();
    const [balance,setBalance] = useState({
        amount:new BigNumber(0),
        rewardDebt:new BigNumber(0),
    });
    const facethBack = useCallback(async () => {
         let _balance = await contrcts.chefContract.methods.userInfo(pid, account).call();
         setBalance({
             amount:new BigNumber(_balance['amount']),
             rewardDebt:new BigNumber(_balance['rewardDebt'])
         });
    }, [contrcts,account,pid]);

    useEffect(()=>{
        facethBack();
    },[
        blockNumber
    ])

    return balance;
}
