import React, {useContext} from "react";
import styled from "styled-components";
import {CellBottom} from "./cell_bottom";
import {Accordion, AccordionDetails, AccordionSummary} from "@material-ui/core";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {StakeContext} from "../../../provider/stake_provider";
import {Contract} from "web3-eth-contract";
import {useUserInfo} from "../../../server/chef";
import {getBalanceFormat} from "../../../utils/formatBalance";
import {useBalance} from "../../../utils/token_server";
import {useWeb3} from "../../../provider/web3_provider";

export const CellTopWidget: React.FC<{
    tokenAddress: string,
    tokenContract: Contract,
    pid: string,
    tokenNick:string,
    img:string
}> = ({
        tokenAddress,
        tokenContract,
        pid,
                                      tokenNick,
                                      img
    }) => {

    const {contrcts} = useWeb3();
    const stakeBalance = useUserInfo(pid);
    const balance = useBalance(tokenContract,contrcts.chefAddress);
    return (
        <StakeContext.Provider value={{address: tokenAddress, contract: tokenContract, pid, ...stakeBalance}}>
            <AccordionStyle>
                <AccordionSummary
                    aria-controls="panel1bh-content"
                    id="panel1bh-header"
                    expandIcon={<ExpandMoreIcon/>}
                >
                    <WapStyle>
                        <div className={'img'}>
                            <img src={img} alt=""/>
                            <div>
                                <h2>{tokenNick}</h2>
                            </div>
                        </div>

                        <div>
                            1196.5%
                        </div>
                        <div>
                            {getBalanceFormat(balance)}
                        </div>
                        <div>
                            {getBalanceFormat(stakeBalance.amount)}
                        </div>
                        <div>
                            {getBalanceFormat(stakeBalance.rewardDebt)} SGT
                        </div>
                    </WapStyle>
                </AccordionSummary>
                <AccordionDetails>
                    <CellBottom/>
                </AccordionDetails>
            </AccordionStyle>


        </StakeContext.Provider>
    );
}


const AccordionStyle = styled(Accordion)`
  &.MuiPaper-elevation1 {
    box-shadow: none;

  }
`
const WapStyle = styled.div`

  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  .img {
    flex: 4;
    display: flex;
    align-items: center;

    img {
      width: 40px;
      height: 40px;
      display: block;
      border-radius: 100px;
      margin-right: 20px;
    }

    div {
      h2 {
        font-size: 16px;
        font-weight: 400;
        color: #090B4B;
      }

      h3 {
        font-size: 14px;
        font-weight: 400;
        color: #090B4B;
        opacity: 0.6;

        span {
          color: #2F35E5;
        }
      }
    }
  }

  div {
    flex: 1;
    font-size: 14px;
    font-weight: 400;
    color: #090B4B;
  }
`
