import React, {useCallback} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";
import {useForm} from "react-hook-form";
import {StakeInputWidget} from "../../../widget/base/stake_input";
import {StakeButtonWidget} from "./button";
import {useWeb3} from "../../../provider/web3_provider";
import {useBalance} from "../../../utils/token_server";
import {useStakeContext} from "../../../provider/stake_provider";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import BigNumber from "bignumber.js";
import {useRedeem} from "../../../server/sunder";
import {useSendModal} from "../../../widget/modal/send_modal";
import {useDeposit} from "../../../server/chef";

export const BottomRight: React.FC = () => {
    const {register, errors, setValue, handleSubmit} = useForm();

    const {contract, pid,address} = useStakeContext();
    const balance = useBalance(contract);
    const maxNumber = getBalanceNumber(balance);

    // 提交请求
    const [facethDeposit] = useDeposit();

    const sendFn = useSendModal(facethDeposit);
    const handleLogin = useCallback(async (data: any) => {
        let _number = getMaxNumber(data['amount'], new BigNumber(balance));
        await sendFn(pid, _number)
    }, [balance, pid]);

    return (
        <GridStyle item md={6}>
            <div>
                <TopStyle>
                    <p>Deposit</p>
                    <span>{getBalanceFormat(balance)} Available</span>
                </TopStyle>


                <div className={'padding'}>
                    <StakeInputWidget label={'Amount to stake'}
                                      name={'amount'}
                                      setValue={(ev) => {
                                          setValue(ev, maxNumber)
                                      }}
                                      errors={errors}
                                      inputRef={register({
                                          required: 'no Enter the number is dame',
                                          max: {value: maxNumber, message: `maxNumber ${maxNumber}`}
                                      })}
                                      placeholder={'0.00'}
                    />
                </div>
                <StakeButtonWidget
                    onClick={handleSubmit(handleLogin)}
                    text={'Confirm Deposit'}
                />


                <div className={'bottom'}>
                    <h2>
                        My Pool Share 0%
                        <a href={`https://app.uniswap.org/#/add/0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa/${address}`}>Go to Uniswap Pool</a>
                    </h2>
                    <p>Once rewards end,you must claim your rewards within 7 days or they
                        will be forfeited.</p>
                </div>
            </div>


        </GridStyle>
    );
}

const GridStyle = styled(Grid)`
  display: flex;

  &::after {
    content: " ";
    display: block;
    width: 1px;

    background: linear-gradient(0deg, rgba(37, 28, 119, 0) 0%, #251C77 50%, rgba(37, 28, 119, 0) 100%);
    opacity: 0.4;
  }

  > div {
    margin-right: 25px;
  }

  box-sizing: border-box;


  .padding {
    padding: 20px 0;
  }

  .bottom {
    border-top: 1px solid #D7DBE1;
    margin-top: 20px;
    padding-top: 20px;

    h2 {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      font-weight: 400;
      color: #7F7F99;

      a {
        color: #2F35E5;
      }
    }

    p {
      width: 70%;
      margin: 0 auto;
      font-size: 14px;
      font-weight: 400;
      color: #F39010;
      line-height: 18px;
      padding-top: 14px;
      text-align: center;
    }
  }
`

const TopStyle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #D7DBE1;


  p {
    font-size: 14px;
    font-weight: 400;
    color: #7F7F99;
  }

  span {
    font-size: 14px;
    font-weight: 400;
    color: #090B4B;
  }
`
