import React from "react";
import {createStyles, makeStyles, MenuItem, Select, Theme} from "@material-ui/core";
import styled from "styled-components";
import {TokenInterFace} from "../../web3/token.config";
import {formatAddress} from "../../utils/formatBalance";
import {AppConfig} from "../../config";
import {useWallet} from "use-wallet";

const useStyles = makeStyles((theme: Theme) => createStyles({
    paper: {
        border: "1px solid #D5D6E9",
        padding: "0 10px",
        boxSizing: "border-box",

    },
    list: {
        padding: "0",
        "& li": {
            borderBottom: "1px solid #E7E9ED",
            padding: "10px 0",
            "&.Mui-selected": {
                backgroundColor: "transparent"
            }
        },
        "& li:last-child": {
            borderBottom: "none"
        }
    }

}));

export const SelectWidget: React.FC<{
    listToken: { [key: string]: TokenInterFace },
    value: string, setValue: Function
}> =
    ({listToken,value,setValue}) => {
        const classes = useStyles();

        const {chainId} = useWallet();

        return (
            <SelectStyle>
                <Select
                    labelId="demo-simple-select-label"
                    className={'label'}
                    MenuProps={{
                        anchorOrigin: {
                            vertical: "bottom",
                            horizontal: "left"
                        },
                        classes: {
                            paper: classes.paper,
                            list: classes.list,
                        },
                        getContentAnchorEl: null,

                    }}
                    value={value}

                    onChange={(ev)=>{
                        setValue(ev.target.value);
                    }}
                    fullWidth={true}
                >
                    {
                        Object.keys(listToken).map((ev) => {
                            return (
                                <MenuItem value={ev} key={ev}>
                                    <TestStyle>
                                        <img
                                            src={listToken[ev].logo}
                                            alt=""/>
                                        {listToken[ev].nick}
                                    </TestStyle>
                                </MenuItem>
                            );
                        })
                    }
                </Select>

                <div className={'bottom'}>
                    <p>Token Contract: </p> <a href={AppConfig.etherscan[chainId||42]+'address/'+listToken[value].address}>{formatAddress(listToken[value].address)}</a>
                </div>
            </SelectStyle>

        );
    }


const SelectStyle = styled.div`
  .label {
    border: 1px solid #272CBC;
    border-radius: 8px;
    margin: 10px 0;
  }

  .MuiInput-underline:after, .MuiInput-underline:before {
    display: none;
  }

  .bottom {
    display: flex;
    justify-content: flex-end;
    height: 14px;
    font-size: 14px;
    font-weight: 400;
    color: #090B4B;

    span {
      color: #2F35E5;
    }
  }
`


const TestStyle = styled.div`
  padding: 0 20px;
  box-sizing: border-box;
  display: flex;
  align-items: center;

  img {
    display: block;
    width: 30px;
    margin-right: 10px;
  }
`
