export const StakePage: React.FC = () => {

    const {tokenMap,} = useWeb3();

    const [key, setKey] = useState(Object.keys(tokenMap)[0]);

    const tokenItem: TokenInterFace = tokenMap[key];
    const tokens = useGetTokneAddress(tokenItem.address);




    return (

        <WapWidget title={'All Reward Pools'}>
            <MainStyle>
                <h2 className={'title'}>There are two pools where you may participate in LP rewards mining. Deposit your dToken/DAI or eToken/DAI pairs to Balancer and receive your Uniswap LP tokens.
                    <a href="https://sunderfinance.medium.com/introducing-sunder-protocol-8ace93a101ac">User Guide</a>
                </h2>


                <div className={'protocol'}>
                    <div className={'right'}>
                        <p>Protocol</p>
                        <FormControl id={'demo-customized-select-label'}>
                            <Select
                                labelId="demo-customized-select-label"
                                id="demo-customized-select"
                                value={key}
                                MenuProps={{
                                    anchorOrigin: {
                                        vertical: "bottom",
                                        horizontal: "left"
                                    },
                                    getContentAnchorEl: null,
                                }}
                                onChange={(ev: any) => {
                                    setKey(ev.target.value);
                                }}
                            >
                                {
                                    Object.keys(tokenMap).map((ev) => {
                                        return (
                                            <MenuItem key={ev} value={ev}>{tokenMap[ev].nick}</MenuItem>
                                        );
                                    })
                                }
                            </Select>
                        </FormControl>
                    </div>
                </div>

                    <div className={'bar'}>
                        <div style={{flex: 4}}>
                            Rewards Pool
                        </div>

                        <div>
                            Pool APY
                        </div>
                        <div>
                            Total Deposited
                        </div>
                        <div>
                            My Deposits
                        </div>
                        <div>
                            My Rewards
                        </div>

                    </div>

                    <div className={'box'}>
                        <CellTopWidget img={tokenItem.logo} tokenNick={'d'+tokenItem.stakeName + '/DAI Uniswap LP'} pid={tokenItem.apid} tokenAddress={tokens.token1} tokenContract={tokens.token1Contract}/>
                    </div>
                    <div style={{border: "none"}} className={'box'}>
                        <CellTopWidget img={tokenItem.logo} tokenNick={'e'+tokenItem.stakeName+'/DAI Uniswap LP'} pid={tokenItem.vpid} tokenAddress={tokens.token2} tokenContract={tokens.token2Contract}/>
                    </div>


            </MainStyle>


        </WapWidget>
    );
}


